___CSS_LOADER_EXPORT___.push([module.id, `.blog .content > h1 {
  float: left;
  margin-right: 30px;
}
.blog .list {
  border-left: 2px solid #ccc;
  padding: 30px 0 0 120px;
  overflow: hidden;
  margin-left: 20px;
  margin-top: 20px;
  min-height: 300px;
}
.blog .list li {
  margin-bottom: 80px;
}
.blog .list li h1 {
  font-weight: normal;
  margin: 0;
}
.blog .list li p {
  color: gray;
  padding: 40px 0;
  line-height: 1.6;
  text-indent: 2em;
}
.blog .list.article h1 {
  margin-top: 0;
  text-align: center;
}
.blog .list.article h2 {
  text-align: right;
  color: gray;
  margin: 20px 0;
}
.blog .list.article p {
  padding: 20px 0;
  line-height: 1.9;
  text-indent: 2em;
}
.blog .list.article li {
  margin-bottom: 30px;
  margin-left: 50px;
  list-style: disc;
}
